body {
  margin: 0;
  font-family: Zilla Slab Highlight;
}

code {
  font-family: Zilla Slab Highlight;
}

* {
  font-family: Zilla Slab Highlight;
}

.App {
  height: 100vh;
  width: 100%;
  display: flex;
}
.navigation-bar {
  position: fixed;
  width: 20%;
  height: 100%;
  background: #212529;
}

.showing {
  padding-left: 20%;
  width: 80%;
}

.tabs {
  text-align: center;
  height: 40%;
}

.tabs p {
  font-size: 20px;
  font-weight: 500;
  color: hsla(0, 0%, 98%, 0.9);
  cursor: pointer;
}

.tabs p.selected {
  color: rgb(167, 34, 34);
}

.page-container {
  height: 100vh;
  width: 100%;
  background-color: #343a40 !important;
}

.profile {
  text-align: center;
  height: 60%;
  margin-top: 10%;
}

.profile p {
  color: hsla(0, 0%, 98%, 0.9);
  font-size: 25px;
  font-weight: 600;
}

.photo {
  border-radius: 20px;
}

.photo img {
  width: 50%;
  height: 50%;
  border-radius: 50%;
  border-color: #343a40 !important;
  border: 15px solid;
}

.navigation-footer {
  height: 30%;
  display: flex;
  color: "white";
  text-align: center;
  align-items: center;
  justify-content: center;
  gap: 10%;
  margin-left: 10%;
  margin-right: 10%;
}

.icon :hover {
  cursor: pointer;
}

.typewriter {
  font-size: 55px;
  font-weight: 700;
  color: hsla(0, 0%, 98%, 0.9);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 80%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-left: 10%;
}

.typewriter p {
  margin-bottom: 20px;
  font-size: 30px;
  font-weight: 600;
}

.aboutme-container {
  color: hsla(0, 0%, 98%, 0.9);
  padding-top: 2%;
  padding-left: 5%;
  padding-right: 5%;
}

.text-container {
  width: 60%;
  margin-right: 10%;
  height: 50%;
  max-height: 50%;
  padding: 2%;
}

.aboutme-details {
  width: 40%;
}

.aboutme-block {
  display: flex;
  height: 70%;
  max-height: 70%;
}

.aboutme-details p {
  font-size: 20px;
  font-weight: 600;
  border-bottom: #6a7075 solid 1px;
}

.text-title {
  text-align: center;
  font-size: 70px;
  font-weight: 1000;
  color: hsla(0, 0%, 98%, 0.9);
}

.text {
  font-size: 20px;
  font-weight: 500;
}

.button-85 {
  margin-top: 3%;
  padding: 0.6em 2em;
  border: none;
  outline: none;
  color: rgb(255, 255, 255);
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-85:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing-button-85 20s linear infinite;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

@keyframes glowing-button-85 {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

.button-85:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #222;
  left: 0;
  top: 0;
  border-radius: 10px;
}

.skill-logos {
  padding: 10%;
  align-items: center;
  text-align: center;
}

.skill-logo {
  width: 10%;
  height: 10%;
  padding: 1%;
}

.skills-container {
  padding: 2%;
}

.single-info {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-container {
  color: hsla(0, 0%, 98%, 0.9);
  padding-top: 2%;
  padding-left: 5%;
  padding-right: 5%;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.single-text {
  margin-left: 2%;
  color: hsla(0, 0%, 98%, 0.9);
}

.contact-title {
  font-size: 30px;
  font-weight: 700;
  color: hsla(0, 0%, 98%, 0.9);
}

.resume-container {
  padding-top: 1%;
  padding-left: 5%;
  padding-right: 5%;
}

.resume {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  text-align: center;
}

.resume-education,
.resume-experience {
  width: 100%;
}

.single-resume {
  background-color: #212529 !important;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  border: #222 solid 5px;
  height: max-content;
}

.long {
  color: hsla(0, 0%, 98%, 0.9);
  font-weight: 700;
  background-color: rgb(167, 34, 34);
  border-radius: 10px;
  padding: 4px;
  height: max-content;
  width: max-content;
}

.field {
  font-size: 22px;
  font-weight: 1000;
  color: hsla(0, 0%, 98%, 0.9);
}

.company {
  font-size: 20px;
  font-weight: 800;
  color: rgb(167, 34, 34);
}

.second-title {
  font-size: 35px;
  font-weight: 800;
  color: hsla(0, 0%, 98%, 0.9);
}
@media only screen and (min-width: 768px) {
  .resume-education,
  .resume-experience {
    width: 50%;
  }
}
